@import url('https://fonts.googleapis.com/css2?family=Gentium+Book+Plus:wght@700&family=Poppins:ital,wght@0,100;0,500;1,900&display=swap');
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
html{
  scroll-behavior: smooth;
}