.staticBG{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #10172a;
    z-index: -1;
    display: flex;
    flex-direction: column;
}
.stickBackGroundName,.stickBackGroundProf,.linksIcon{
    color: antiquewhite;
    position: absolute;
    z-index: 1;
    transform: translate(-50%,-50%);
}
.stickBackGroundName{
    width: 600px;
    top: 40%;
    left: 65%;
    font-size: 4em;
}
.stickBackGroundProf{
    font-size: 2em;
    width: 600px;
    top: 46%;
    left: 65%;
}
.linksIcon{
    top: 53%;
    left: 65%;
    display: flex;
    gap: 10px;
    align-items: center;
    
    
}
.myResume{
    background: rgb(0,151,230);
    background: linear-gradient(0deg, rgba(0,151,230,1) 0%, rgba(45,105,253,1) 81%);
    padding: 10px;
    border-radius: 10px;
    text-decoration: none;
    color: antiquewhite;
    transition: 2000ms;
}
.linksIcon img{
    height: 6vh;
}
.linksIcon a:hover{
    scale: .98;
    
}


@import url('https://fonts.googleapis.com/css?family=Montserrat');

@media (max-width:800px) {
    /* smartphones, Android phones, landscape iPhone */

    .stickBackGroundName{
        width: 600px;
        top: 65%;
        left: 50%;
        font-size: 2.5em;
    }
    .stickBackGroundProf{
        font-size: 1.2em;
        width: 600px;
        top: 70%;
        left: 50%;
    }
    .linksIcon{
        top: 80%;
        left: 50%;
        display: flex;
        gap: 10px;
        align-items: center;
    }
    .myResume{
        background: rgb(0,151,230);
        background: linear-gradient(0deg, rgba(0,151,230,1) 0%, rgba(45,105,253,1) 81%);
        padding: 10px;
        border-radius: 10px;
        text-decoration: none;
        color: antiquewhite;
        transition: 2000ms;
        width: 20vw;
        font-size: 2vw;
    }
    .linksIcon img{
        height: 8vh;
    }
    .linksIcon a:hover{
        scale: .98;
        
    }
}