.card{
    position: absolute;
    border-radius: 10px;
    transform-style: preserve-3d;
    margin: 0 !important;
    transform: translate(-50%,-50%);
    width: 20.8vw;
    height: 30.3vw;
}
#monstercard{
    top: 18%;
    left: 25%;
}

.front{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    visibility: visible;
    -webkit-backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.cardFront{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}
#back{
    position: absolute;
    top: 0;
    left: 0;
    width: 400px;
    height: 580px;
    border-radius: 8px;
    z-index: -2;
    -webkit-backface-visibility: hidden;
    transform: rotateY(180deg);
}
.title--place{
    display: flex;
    margin-top: 1.4vw;
    height: 2.1vw;
    justify-content: space-between;
    align-items: center;
    width: 85%;
}
#cardTitle{
    font-family: 'Gentium Book Plus', serif;
    font-size: 1.9vw;
    margin-left: .1vw;
    text-shadow:0 .5px 0 #fff;
}
#cardType {
    height: 1.8vw;
    margin-right: .2vw;
    
}

.star--place{
    display: flex;
    flex-direction: row-reverse;
    margin-top: .3vw;
    width: 80%;
}
/* star levels */
#starLevel,
#starLevel2,
#starLevel3,
#starLevel4,
#starLevel5,
#starLevel6,
#starLevel7,
#starLevel8,
#starLevel9,
#starLevel10,
#starLevel11,
#starLevel12 {
    width: 1.2vw;
}
#starLevel5,
#starLevel6,
#starLevel7,
#starLevel8,
#starLevel9,
#starLevel10,
#starLevel11,
#starLevel12 {
    visibility: hidden;
}

.mypic{
    margin-top: .65vw;
    width: 75.5%;
    height: 52%;
    overflow: hidden;
}
.mypic img{
    width: 100%;
}
.desc--place{
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-top: 1.4vw;
    height: 20%;
    width: 85%;
}
#monsterType {
    margin-top: 0;
    font-size: .8vw;
}
#monsterDescription {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    font-size: 0.6vw;
    width: 100%;
    font-style: italic;
    text-align: left;
    margin-top: -.9vw;
}
.att-def{
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: 10%;
    align-items: center;
    margin-top: -0.2vw;
}
#monsterAtk {
    font-size: .8vw;
    font-weight: bold;
    margin-right: 3.5vw;
}
#monsterDef {
    font-size: .8vw;
    font-weight: bold;
    margin-right: 1vw;
}


@media (max-width:800px)  {
     /* smartphones, Android phones, landscape iPhone */ 
     #monstercard{
        top: 30vw;
        left: 26vw;
        transform: translate(-50%,-50%);
    }

    .card{
        width: 50vw;
        height: 80vw;
    }

    .front{
        width: 100%;
        height: 100%;
    }
    .title--place{
        display: flex;
        margin-top: 3.5vw;
        height: 5vw;
        justify-content: space-between;
        align-items: center;
        width: 85%;
    }
    #cardTitle{
        font-family: 'Gentium Book Plus', serif;
        font-size: 4vw;
        margin-left: .1vw;
        text-shadow:0 .5px 0 #fff;
    }
    #cardType {
        height: 4vw;
        margin-right: .2vw;
        
    }
    
    .star--place{
        display: flex;
        flex-direction: row-reverse;
        margin-top: 1.5vw;
        width: 80%;
    }
    /* star levels */
    #starLevel,
    #starLevel2,
    #starLevel3,
    #starLevel4,
    #starLevel5,
    #starLevel6,
    #starLevel7,
    #starLevel8,
    #starLevel9,
    #starLevel10,
    #starLevel11,
    #starLevel12 {
        width: 3vw;
    }
    #starLevel5,
    #starLevel6,
    #starLevel7,
    #starLevel8,
    #starLevel9,
    #starLevel10,
    #starLevel11,
    #starLevel12 {
        visibility: hidden;
    }
    
    .mypic{
        margin-top: 1.6vw;
        width: 75.7%;
        height: 41.6vw;
        overflow: hidden;
    }
    .mypic img{
        height: 100%;
    }
    .desc--place{
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-top: 4.5vw;
        height: 20%;
        width: 85%;
    }
    #monsterType {
        margin-top: 0;
        font-size: 2vw;
    }
    #monsterDescription {
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-weight: 500;
        font-size: 1.6vw;
        width: 100%;
        font-style: italic;
        text-align: left;
        margin-top: -3vw;
    }
    .att-def{
        display: flex;
        justify-content: flex-end;
        width: 100%;
        height: 10%;
        align-items: center;
        margin-top: 0vw;
    }
    #monsterAtk {
        font-size: 2vw;
        font-weight: bold;
        margin-right: 8.8vw;
    }
    #monsterDef {
        font-size: 2vw;
        font-weight: bold;
        margin-right: 3vw;
    }
}